import MENU from './menu.html';
import PAYMENT from './payment.html';
import styles from './styles.css';
import Stripe from 'stripe';

function payment(document) {
    let payment = document.createElement('div');
    payment.innerHTML = PAYMENT;
    document.body.appendChild(payment);

    const stripe = Stripe(
        'pk_test_51OMz8fEd2D1v95xdiuz0xySnlFwIrNZbc1aSBgN6I0yG196CM3qVp8NopNrTykIJapfMPVgd7IrRoG7tvfXa3p5E00nyVD19qU',
        {
            apiVersion: '2020-08-27',
        }
    );

    const paymentRequest = stripe.paymentRequest({
        country: 'US',
        currency: 'usd',
        total: {
            label: 'Revive-Froggy',
            amount: 100,
        },
        requestPayerName: true,
    });

    // express checkout element
    const appearance = {
        /* appearance */
    };
    const options = {
        /* options */
    };
    const elements = stripe.elements({
        mode: 'payment',
        amount: 100,
        currency: 'usd',
        appearance,
    });
    const expressCheckoutElement = elements.create('expressCheckout', options);
    expressCheckoutElement.mount('#express-checkout-element');

    document
        .getElementById('payment-request-button')
        .addEventListener('click', () => {
            fetch('/create-payment-intent', {
                method: 'POST',
            })
                .then((response) => response.json())
                .then((data) => {
                    return stripe.confirmCardPayment(data.clientSecret, {
                        payment_method: {
                            // Provide a payment method (e.g., card details)
                        },
                    });
                })
                .then((result) => {
                    if (result.error) {
                        console.log('Payment failed:', result.error);
                    } else {
                        console.log('Payment successful!');
                    }
                });
        });
}

// initialize menu
export function init_pages(window, document) {
    // ------------------ APPEND PAGES ------------------
    // append menu]
    let menu = document.createElement('div');
    menu.innerHTML = MENU;
    // document.body.appendChild(menu);

    payment(document);

    // append styles
    let style = document.createElement('style');
    style.innerHTML = styles;
    document.head.appendChild(style);

    // --------------------------------------------------

    // event listener for page change
    window.addEventListener('keyup', (event) => {
        if (event.key == 'p') {
            let vis = document.getElementById('menu').style.visibility;
            document.getElementById('menu').style.visibility =
                vis == 'hidden' ? 'visible' : 'hidden';
        }
    });
}

